import { styled } from "styled-components";
import AddButton from "../Icons/AddButton";
import DeleteButton from "../Icons/DeleteButton";
import { useEffect, useState } from "react";
import { message, Modal, Input, Select } from "antd";
import TextArea from "antd/es/input/TextArea";
import {
  ControllerProps,
  FormProps,
  RootState,
  TagListProps,
} from "../../config/types";
import { useDispatch, useSelector } from "react-redux";
import { deleteTagList, setTagList } from "../../features/tagList/tagListSlice";
import { setMemoList } from "../../features/memoList/memoListSlice";
import axios from "axios";
import {
  BAD,
  DUPLICATED,
  NOT_VALID,
  SIZE,
  USER_NOT_FOUND,
} from "../../config/errorCode";
import { useNavigate } from "react-router-dom";

const ADD = "ADD";
const DEL = "DEL";

const Sidebar = () => {
  const [addable, setAddable] = useState(false);
  const [deletable, setDeletable] = useState(false);
  const [tag, setTag] = useState("");
  const [inputCount, setInputCount] = useState(0);
  const [checkedTag, setCheckedTag] = useState("");
  const [memoModalOpen, setMemoModalOpen] = useState(false);
  const [modalForm, setModalForm] = useState<FormProps>({
    keyword: "",
    content: "",
    tag: [],
  });
  const [page, setPage] = useState(0); //   

  const dispatch = useDispatch();
  const tagList = useSelector((state: RootState) => state.tagList);

  const [messageApi, contextHolder] = message.useMessage();
  const { confirm } = Modal;
  const options: object[] = tagList.map((tagVal) => {
    return { label: tagVal, value: tagVal };
  });
  const navigate = useNavigate();

  const isEditing = addable === true || deletable === true ? true : false;

  // 
  const showMessage = (msg: string) => {
    messageApi.open({
      type: "success",
      content: msg,
      duration: 1,
      className: "newTagMsg",
    });
  };

  //  
  const showConfirm = () => {
    confirm({
      icon: null,
      title: "   ?",
      okText: "",
      cancelText: "",
      centered: true,
      onOk() {
        dispatch(deleteTagList(checkedTag));
      },
    });
  };

  //  
  const handleCancelButton = () => {
    if (addable) {
      setTag("");
      setInputCount(0);
      setAddable(false);
    }
    if (deletable) setDeletable(false);
  };

  //  / 
  const handleEditButton = async (type: string) => {
    if (type === ADD) {
      if (tagList.length >= 10) {
        showMessage(" 10   !");
        setTag("");
        setInputCount(0);
        return;
      }
      if (!tag) {
        showMessage("  ");
        return;
      }

      //   ( )
      try {
        const res = await axios.post("/tag", { name: tag });
        if (res.status === 200) {
          setTag("");
          setInputCount(0);
          showMessage("  !");
        }
      } catch (e: any) {
        const { response } = e.response.data;
        if (response === USER_NOT_FOUND) {
          navigate("/");
        }
        if (response === NOT_VALID) {
          showMessage("   ");
        }
      }
    }
    if (type === DEL) {
      if (!checkedTag) return;
      showConfirm();
      setCheckedTag("");

      //   ( )
      try {
        const res = await axios.delete(`/tag?name=${tag}`);
        if (res.status === 200) {
          console.log("  Response", res);
        }
      } catch (error) {
        //  
        console.log("  ", error);
        // USER_NOT_FOUND:   
        // NOT_VALID:   
      }
    }
  };

  //    
  const handleInputCount = (value: string) => {
    setTag(value);
    setInputCount(value.length);
  };

  //     
  const enterHandler = (e: React.KeyboardEvent<HTMLInputElement>) => {
    if (e.key === "Enter") {
      handleEditButton(ADD);
    }
  };

  //   
  const checkHandler = (tag: string) => {
    setCheckedTag(tag);
    if (checkedTag === tag) {
      setCheckedTag("");
    }
  };

  //  
  const onSubmitForm = async () => {
    if (!modalForm.keyword) return;
    dispatch(setMemoList(modalForm));

    //   ( )
    try {
      setModalForm({ ...modalForm, keyword: "", content: "", tag: [] });
      setMemoModalOpen(false);

      const res = await axios.post("/memo", modalForm);
      if (res.status === 200) {
        console.log("  Response", res);
      }
    } catch (e: any) {
      const { response } = e.response.data;
      if (response === DUPLICATED) {
        alert(" .   ");
      }
      if (response === BAD) {
        alert("   .  ");
      }
    }
  };

  //  
  const getTagMemo = async (tagName: string) => {
    //   (  )
    try {
      const res = await axios.get(`/memo?tag=${tagName}&page=${page}`);
      if (res.status === 200) {
        dispatch(setMemoList(res));
        setMemoList(res);
      }
    } catch (e: any) {}
  };

  return (
    <SidebarContainer>
      {contextHolder}
      <Modal
        className="memoAddModal"
        title=""
        centered
        open={memoModalOpen}
        onOk={onSubmitForm}
        onCancel={() => setMemoModalOpen(false)}
        okText=""
        cancelText=""
      >
        <LineWrap>
          <Title>
            <span className="must">*</span> :
          </Title>
          <Input
            placeholder=" "
            showCount
            maxLength={20}
            value={modalForm.keyword}
            onChange={(e) =>
              setModalForm({ ...modalForm, keyword: e.target.value })
            }
          />
        </LineWrap>
        <LineWrap>
          <Title> :</Title>
          <TextArea
            showCount
            maxLength={100}
            value={modalForm.content}
            placeholder="          "
            onChange={(e) =>
              setModalForm({ ...modalForm, content: e.target.value })
            }
          />
        </LineWrap>
        <LineWrap>
          <Title> :</Title>
          <Select
            mode="multiple"
            placeholder=" "
            options={options}
            className="tagSelectInput"
            value={modalForm.tag}
            onSelect={(value) =>
              setModalForm({ ...modalForm, tag: [...modalForm.tag, value] })
            }
            onDeselect={(value) =>
              setModalForm({
                ...modalForm,
                tag: modalForm.tag.filter((v) => v !== value),
              })
            }
          />
        </LineWrap>
      </Modal>
      <TopWrap>
        <h2>All</h2>
        <span></span>
      </TopWrap>
      <TagContainer>
        {!isEditing ? (
          <Controller $isEditing={isEditing}>
            <AddButton onClick={() => setAddable(true)} />
            <DeleteButton onClick={() => setDeletable(true)} />
          </Controller>
        ) : addable ? (
          <AddWrap>
            <Controller $isEditing={isEditing}>
              <TextButton onClick={() => handleCancelButton()}></TextButton>
              <TextButton onClick={() => handleEditButton(ADD)}>
                
              </TextButton>
            </Controller>
            <AddInput
              type="text"
              name="name"
              value={tag}
              maxLength={20}
              onChange={({ target: { value } }) => handleInputCount(value)}
              onKeyDownCapture={(e) => enterHandler(e)}
            />
            <TextCounting>{inputCount} / 20</TextCounting>
          </AddWrap>
        ) : (
          <Controller $isEditing={isEditing}>
            <TextButton onClick={() => handleCancelButton()}></TextButton>
            <TextButton onClick={() => handleEditButton(DEL)}></TextButton>
          </Controller>
        )}
        {deletable ? (
          <TagList $isEditing={addable}>
            {tagList.map((tag, idx) => (
              <TagWrap key={idx}>
                <Tag>#{tag}</Tag>
                <label>
                  <input
                    type="checkbox"
                    checked={tag === checkedTag}
                    onChange={(e) => checkHandler(tag)}
                  />
                  <CheckCircle className="checkbox" />
                </label>
              </TagWrap>
            ))}
          </TagList>
        ) : (
          <TagList $isEditing={addable}>
            {tagList.map((tag, idx) => (
              <Tag key={idx} onClick={() => getTagMemo(tag)}>
                #{tag}
              </Tag>
            ))}
          </TagList>
        )}
      </TagContainer>
      <MemoButton onClick={() => setMemoModalOpen(true)}></MemoButton>
    </SidebarContainer>
  );
};

const SidebarContainer = styled.div`
  position: relative;
`;

const TopWrap = styled.div`
  display: flex;
  flex-direction: column;
  gap: 15px;
  margin-bottom: 44px;
  box-sizing: border-box;
  font-size: 20px;
  line-height: 29px;
  h2 {
    font-weight: 700;
  }
  span {
    font-weight: 400;
  }
`;

const TagContainer = styled.div`
  width: 100%;
`;

const Controller = styled.div<ControllerProps>`
  display: flex;
  gap: ${({ $isEditing }) => (!$isEditing ? `8px` : `16px`)};
  margin-bottom: ${({ $isEditing }) => (!$isEditing ? `17px` : `20px`)};
`;

const TextButton = styled.button`
  font-weight: 500;
  line-height: 24px;
  letter-spacing: -0.176px;
  font-size: 16px;
`;

const AddWrap = styled.div`
  margin-bottom: 25px;
`;

const AddInput = styled.input`
  width: 160px;
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: 20px;
  font-weight: 600;
  line-height: 29px;
  padding-bottom: 4px;
  border-bottom: 1px solid #000;
  &:focus {
    border-bottom: 1px solid #000;
  }
`;

const TextCounting = styled.span`
  display: block;
  width: 160px;
  margin-top: 6px;
  text-align: right;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  letter-spacing: -0.132px;
`;

const TagList = styled.ul<TagListProps>`
  display: flex;
  flex-direction: column;
  row-gap: ${({ $isEditing }) => (!$isEditing ? `12px` : `4px`)};
`;

const TagWrap = styled.div`
  width: fit-content;
  display: flex;
  align-items: center;
  gap: 9px;

  input[type="checkbox"] {
    display: none;
  }
  input[type="checkbox"]:checked + .checkbox {
    background-color: #d9d9d9;
  }
`;

const Tag = styled.li`
  width: 131px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  line-height: 29px;
  cursor: pointer;
`;

const CheckCircle = styled.div`
  width: 16px;
  height: 16px;
  border: 2px solid #000;
  border-radius: 50px;
`;

const MemoButton = styled.button`
  position: fixed;
  bottom: 80px;
  width: 160px;
  display: block;
  padding-bottom: 18px;
  border-bottom: 1px solid #000;

  color: #000;
  font-size: 30px;
  font-weight: 700;
`;

const LineWrap = styled.div`
  display: flex;
  justify-content: center;
  gap: 12px;

  input {
    width: 300px;
  }
`;

const Title = styled.span`
  font-size: 16px;
  .must {
    color: red;
  }
`;

export default Sidebar;
